import React from "react";
import { SocialIcon } from "react-social-icons";

import personImg from "./../../assets/img/pngegg.png";

import "./header.styles.css";

const Header = () => {
  return (
    <header id="header">
      <div className="header-content">
        <div className="container grid-2">
          <div className="column-1">
            <h1 className="header-title">
              Pankaj is a developer and artist who loves to craft a good story
            </h1>
            <div className="line" />
            <p className="text">
              I work on Java, J2EE, and JavaScript. Currently I am a Software
              Developer at
              <a
                href="https://www.opentext.com/"
                target="_blank"
                rel="noreferrer"
              >
                {" "}
                OpenText
              </a>
            </p>
            <div className="social-media">
              <SocialIcon
                url="https://www.linkedin.com/in/pankaj033/"
                bgColor="white"
                className="social-icon"
                target="_blank"
              />
              <SocialIcon
                url="https://github.com/pvyas033"
                bgColor="white"
                className="social-icon"
                target="_blank"
              />
              <SocialIcon
                url="https://twitter.com/pankaj_pvt"
                bgColor="white"
                className="social-icon"
                target="_blank"
              />
            </div>
          </div>
          <div className="column-2 image ">
            <img src={personImg} className="img-element z-index" alt="" />
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
