import { faComment, faDownload, faEnvelope, faMapMarkerAlt, faMobileAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

import resume from "../../assets/pdf/resume.pdf";
import Footer from "../../components/footer/footer.component";
import Notebook from "../../components/notebook/notebook.component";

import "./contact.styles.css";

const Contact = () => {
  return (
    <section className="contact section" id="contact">
      <div className="contact-body">
        <div className="container">
          <div className="header">
            <h3 className="title" data-title="Let's connect">
              Contact
            </h3>
          </div>
          <div className="section">
            <div className="contact-info">
              <FontAwesomeIcon icon={faEnvelope} size="3x" />
              <div className="email">
                <h1>pankajvyas033@gmail.com</h1>
              </div>
            </div>
            <div className="contact-info">
              <FontAwesomeIcon icon={faMobileAlt} size="3x" />
              <div className="email">
                <h1>+91-7354510208</h1>
              </div>
            </div>
            <div className="contact-info">
              <FontAwesomeIcon icon={faMapMarkerAlt} size="3x" />
              <div className="email">
                <h1>Bangalore, India</h1>
              </div>
            </div>
          </div>
          <div className="contact-button-panel">
            <a
              href="https://t.me/pankajvyas033"
              className="btn drop-message"
              target="_blank"
              rel="noreferrer"
            >
              Drop a message {"      "}
              <FontAwesomeIcon icon={faComment} size="1x" />
            </a>
            <a
              href={resume}
              target="_blank"
              className="btn download-resume"
              rel="noreferrer"
            >
              Download Resume {"      "}
              <FontAwesomeIcon icon={faDownload} size="1x" />
            </a>
          </div>
        </div>
        <div className="notebook-add">
          <Notebook />
        </div>
      </div>
      <Footer />
    </section>
  );
};

export default Contact;
